"use client";

import type { Calculation } from "@/lib/types";
import { formatDistanceToNow } from "date-fns";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Icons } from "./icons";
import { cn } from "@/lib/utils";
import { Badge } from "./ui/badge";


type CalculationHistoryProps = {
  history: Calculation[];
  onSelect: (calculation: Calculation) => void;
  onClear: () => void;
  selectedId?: string | null;
};

export function CalculationHistory({ history, onSelect, onClear, selectedId }: CalculationHistoryProps) {
  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between">
        <div>
            <CardTitle>Calculation History</CardTitle>
            <CardDescription>Review your past analyses.</CardDescription>
        </div>
        {history.length > 0 && (
          <Button variant="ghost" size="sm" onClick={onClear}>
            <Icons.Trash2 className="mr-2 h-4 w-4" />
            Clear
          </Button>
        )}
      </CardHeader>
      <CardContent>
        <ScrollArea className="h-64">
          {history.length === 0 ? (
            <div className="flex h-full flex-col items-center justify-center text-center text-muted-foreground">
              <Icons.History className="h-10 w-10" />
              <p className="mt-2 text-sm">No calculations yet.</p>
              <p className="text-xs">Your history will appear here.</p>
            </div>
          ) : (
            <div className="space-y-2 pr-4">
              {history.map((item) => (
                <button
                  key={item.id}
                  onClick={() => onSelect(item)}
                  className={cn(
                    "flex w-full cursor-pointer items-center justify-between rounded-md border p-3 text-left transition-colors hover:bg-accent/50",
                    selectedId === item.id && "bg-accent/80"
                  )}
                >
                    <div>
                        <p className="font-semibold">{item.productName}</p>
                        <p className="text-xs text-muted-foreground">
                            {formatDistanceToNow(new Date(item.createdAt), { addSuffix: true })}
                        </p>
                    </div>
                    <div className="flex items-center gap-2">
                       <Badge variant={item.isProfitable ? "default" : "destructive"} className={cn("hidden sm:inline-flex", item.isProfitable ? "bg-green-600" : "")}>
                         {item.profitMargin.toFixed(1)}%
                       </Badge>
                       <Icons.ArrowRight className="h-4 w-4 text-muted-foreground"/>
                    </div>
                </button>
              ))}
            </div>
          )}
        </ScrollArea>
      </CardContent>
    </Card>
  );
}
