
"use client";

import type { Calculation } from "@/lib/types";
import { Badge } from "@/components/ui/badge";
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card";
import { Icons } from "./icons";
import { Skeleton } from "./ui/skeleton";
import { Separator } from "./ui/separator";

type CalculationResultProps = {
  calculation: Calculation | null;
  isLoading: boolean;
};

const StatCard = ({ title, value, icon: Icon, valueClassName }: { title: string; value: string; icon: React.ElementType, valueClassName?: string }) => (
    <div className="flex items-center gap-4 rounded-lg bg-muted/10 p-4">
        <div className="rounded-full bg-primary/10 p-3">
             <Icon className="h-6 w-6 text-primary" />
        </div>
        <div>
            <p className="text-sm text-muted-foreground">{title}</p>
            <p className={`text-2xl font-bold ${valueClassName}`}>{value}</p>
        </div>
    </div>
)

export function CalculationResult({ calculation, isLoading }: CalculationResultProps) {
  if (isLoading) {
    return (
        <Card className="sticky top-24">
            <CardHeader>
                <Skeleton className="h-8 w-3/4" />
                <Skeleton className="h-4 w-1/2" />
            </CardHeader>
            <CardContent className="space-y-6">
                <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
                    <Skeleton className="h-24 w-full" />
                    <Skeleton className="h-24 w-full" />
                </div>
                <div className="space-y-2">
                    <Skeleton className="h-6 w-1/4" />
                    <Skeleton className="h-20 w-full" />
                </div>
            </CardContent>
        </Card>
    )
  }
  
  if (!calculation) {
    return (
      <Card className="sticky top-24 flex min-h-[400px] flex-col items-center justify-center text-center">
        <CardHeader>
          <div className="mx-auto rounded-full bg-primary/10 p-4">
            <Icons.Logo className="h-12 w-12 text-primary" />
          </div>
          <CardTitle className="mt-4 text-2xl">Ready for Analysis</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-muted-foreground">
            Fill out the form to calculate a product's profit margin and get AI-powered insights.
          </p>
        </CardContent>
      </Card>
    );
  }

  const {
    productName,
    profitMargin,
    profitAmount,
    isProfitable,
    analysis,
    wholesalePrice,
    retailPrice
  } = calculation;

  const profitMarginColor = isProfitable ? "text-green-600" : "text-destructive";

  return (
    <Card className="sticky top-24">
      <CardHeader>
        <div className="flex items-start justify-between">
            <div>
                <CardTitle className="font-headline text-3xl">{productName}</CardTitle>
                <CardDescription>
                Profitability analysis based on your inputs.
                </CardDescription>
            </div>
            <Badge variant={isProfitable ? "default" : "destructive"} className={`${isProfitable ? 'bg-green-600 hover:bg-green-700' : ''} text-white`}>
                {isProfitable ? "Profitable" : "Not Profitable"}
            </Badge>
        </div>
      </CardHeader>
      <CardContent className="space-y-6">
        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2">
            <StatCard 
                title="Profit Margin" 
                value={`${profitMargin.toFixed(2)}%`}
                icon={Icons.BadgePercent}
                valueClassName={profitMarginColor}
            />
             <StatCard 
                title="Profit Amount" 
                value={`₹${profitAmount.toFixed(2)}`}
                icon={Icons.TrendingUp}
                valueClassName={profitMarginColor}
            />
        </div>

        <div>
            <h3 className="mb-2 flex items-center font-semibold"><Icons.Sparkles className="mr-2 h-5 w-5 text-accent" /> AI-Powered Analysis</h3>
            <p className="rounded-lg border bg-muted/50 p-4 text-sm text-muted-foreground">
            {analysis}
            </p>
        </div>
      </CardContent>
      <CardFooter className="flex-col items-start gap-2 text-sm text-muted-foreground">
        <Separator className="mb-2"/>
        <p><strong>Wholesale Price:</strong> ₹{wholesalePrice.toFixed(2)} (from {calculation.wholesaleSource})</p>
        <p><strong>Retail Price:</strong> ₹{retailPrice.toFixed(2)} (on {calculation.retailSource})</p>
      </CardFooter>
    </Card>
  );
}
