
"use client";

import { zodResolver } from "@hookform/resolvers/zod";
import { useForm } from "react-hook-form";
import { z } from "zod";

import { Button } from "@/components/ui/button";
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Icons } from "./icons";
import type { CalculateProfitMarginInput } from "@/ai/flows/calculate-profit-margin";

const formSchema = z.object({
  productName: z.string().min(1, "Product name is required."),
  wholesalePrice: z.coerce.number({invalid_type_error: "Must be a number"}).min(0, "Wholesale price must be non-negative."),
  retailPrice: z.coerce.number({invalid_type_error: "Must be a number"}).min(0, "Retail price must be non-negative."),
  wholesaleSource: z.string().min(1, "Wholesale source is required.").default("Local Market"),
  retailSource: z.string().min(1, "Retail source is required.").default("Amazon"),
});

type ProfitCalculatorProps = {
  onSubmit: (values: CalculateProfitMarginInput) => void;
  isLoading: boolean;
};

export function ProfitCalculator({ onSubmit, isLoading }: ProfitCalculatorProps) {
  const form = useForm<z.infer<typeof formSchema>>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      productName: "",
      wholesalePrice: '' as any,
      retailPrice: '' as any,
      wholesaleSource: "Local Market",
      retailSource: "Amazon",
    },
  });

  return (
    <Card>
      <CardHeader>
        <CardTitle>Profit Margin Calculator</CardTitle>        <CardDescription>
          Enter product details to analyze profit potential.
        </CardDescription>
      </CardHeader>
      <CardContent>
        <Form {...form}>
          <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
            <FormField
              control={form.control}
              name="productName"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Product Name</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g. Organic Cotton T-Shirt" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <div className="grid grid-cols-1 gap-4 md:grid-cols-2">
                <FormField
                control={form.control}
                name="wholesalePrice"
                render={({ field }) => (
                    <FormItem>
                    <FormLabel>Wholesale Price (₹)</FormLabel>
                    <FormControl>
                        <Input type="number" placeholder="e.g. 550" {...field} />
                    </FormControl>
                    <FormMessage />
                    </FormItem>
                )}
                />
                <FormField
                control={form.control}
                name="retailPrice"
                render={({ field }) => (
                    <FormItem>
                    <FormLabel>Online Retail Price (₹)</FormLabel>
                    <FormControl>
                        <Input type="number" placeholder="e.g. 1999" {...field} />
                    </FormControl>
                    <FormMessage />
                    </FormItem>
                )}
                />
            </div>
            <FormField
              control={form.control}
              name="wholesaleSource"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Wholesale Source</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g. Local Supplier" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <FormField
              control={form.control}
              name="retailSource"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Online Retailer</FormLabel>
                  <FormControl>
                    <Input placeholder="e.g. Amazon, Flipkart" {...field} />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
            <div className="flex flex-col gap-2 sm:flex-row">
              <Button type="submit" disabled={isLoading} className="w-full">
                {isLoading ? (
                  <Icons.Loader className="animate-spin" />
                ) : (
                  <Icons.Sparkles className="mr-2 h-4 w-4" />
                )}
                Analyze Profit
              </Button>
               <Button type="button" variant="outline" disabled className="w-full">
                <Icons.Camera className="mr-2 h-4 w-4" />
                Scan Receipt (Coming Soon)
              </Button>
            </div>
          </form>
        </Form>
      </CardContent>
    </Card>
  );
}
