
"use client";

import * as React from "react";
import { getProfitMargin } from "@/app/actions";
import { useToast } from "@/hooks/use-toast";
import type { Calculation } from "@/lib/types";
import { ProfitCalculator } from "@/components/profit-calculator";
import { CalculationResult } from "@/components/calculation-result";
import { CalculationHistory } from "@/components/calculation-history";
import type { CalculateProfitMarginInput } from "@/ai/flows/calculate-profit-margin";
import { Button } from "@/components/ui/button";
import Link from "next/link";
import { Icons } from "@/components/icons";

export default function CalculatorPage() {
  const { toast } = useToast();
  const [isLoading, setIsLoading] = React.useState(false);
  const [history, setHistory] = React.useState<Calculation[]>([]);
  const [selectedCalculation, setSelectedCalculation] = React.useState<Calculation | null>(null);

  const handleFormSubmit = async (values: CalculateProfitMarginInput) => {
    setIsLoading(true);
    setSelectedCalculation(null); // Clear previous selection

    const result = await getProfitMargin(values);

    setIsLoading(false);

    if (result.error) {
      toast({
        variant: "destructive",
        title: "Error",
        description: result.error,
      });
      return;
    }

    if (result.data) {
      const newCalculation: Calculation = {
        ...values,
        ...result.data,
        id: new Date().toISOString(),
        createdAt: new Date().toISOString(),
      };
      setHistory((prev) => [newCalculation, ...prev]);
      setSelectedCalculation(newCalculation);
    }
  };
  
  const handleSelectHistory = (calculation: Calculation) => {
    setSelectedCalculation(calculation);
  };

  const handleClearHistory = () => {
    setHistory([]);
    setSelectedCalculation(null);
  }

  return (
    <div className="min-h-screen w-full bg-background">
      <header className="sticky top-0 z-10 w-full border-b bg-background/80 backdrop-blur-md">
            <div className="container mx-auto flex h-16 items-center justify-between px-4 md:px-6">
                <div className="flex items-center gap-2">
                    <Icons.Logo className="h-8 w-8 text-primary" />
                    <h1 className="font-headline text-2xl font-bold text-primary">BharatAI Bazaar</h1>
                </div>
                 <div className="flex items-center gap-4">
                    <Button variant="outline" size="sm" asChild>
                        <Link href="/seller">Back to Dashboard</Link>
                    </Button>
                    <Button variant="ghost" size="sm" asChild>
                        <Link href="/login">Logout</Link>
                    </Button>
                </div>
            </div>
        </header>
      <main className="container mx-auto flex-1 p-4 md:p-6">
        <div className="grid grid-cols-1 gap-8 lg:grid-cols-3">
          <div className="lg:col-span-2">
            <CalculationResult calculation={selectedCalculation} isLoading={isLoading} />
          </div>
          <div className="space-y-6">
            <ProfitCalculator onSubmit={handleFormSubmit} isLoading={isLoading} />
            <CalculationHistory 
                history={history}
                onSelect={handleSelectHistory}
                onClear={handleClearHistory}
                selectedId={selectedCalculation?.id}
            />
          </div>
        </div>
      </main>
    </div>
  );
}
